<template>
  <div class="container content">
    <div class="row">
      <div class="col-12">
        <h1 class="text-primary">Transaksi Tagihan Listrik</h1>
      </div>
    </div>
    <div class="row mt-3">
      <div v-show="!this.noPlNValid" class="col-12 mt-2">
        <div class="alert alert-danger rounded-10" role="alert">
          Masukkan no PLN yang valid!
        </div>
      </div>
      <template v-if="this.hasilCheck">
        <div v-if="this.hasilCheck.responseCode == 400" class="col-12 mt-2">
          <div class="alert alert-primary rounded-10" role="alert">
            {{ this.hasilCheck.responseData.description }}
          </div>
        </div>
      </template>
      <div class="col-12">
        <div class="card bg-putih shadow rounded-10">
          <div class="card-body">
            <form class="text-left">
              <div class="form-group">
                <label for="no_tujuan" class="text-primary">ID PLN</label>
                <input
                  @input="checkDigit"
                  v-model="this.noTujuan"
                  id="no_tujuan"
                  required=""
                  autocomplete="off"
                  type="text"
                  class="
                    form-control form-control-lg
                    border border-primary
                    rounded-10
                  "
                  name="no_hp"
                  placeholder="Masukkan Nomor PLN.."
                />
              </div>
              <div>
                <button
                  v-if="!this.loadCheckSpinner && this.noPlNValid"
                  @click="checkTagihan()"
                  type="button"
                  class="btn btn-lg btn-block btn-primary rounded-10"
                >
                  Cek Tagihan
                </button>
                <button
                  v-if="this.loadCheckSpinner"
                  type="button"
                  class="btn btn-lg btn-block btn-primary rounded-10"
                  disabled
                >
                  <span
                    class="spinner-border spinner-border-sm"
                    role="status"
                    aria-hidden="true"
                  ></span>
                  Loading...
                </button>
              </div>
              <template v-if="this.hasilCheck">
                <div
                  v-if="this.hasilCheck.responseCode == 200"
                  class="card rounded-10 mt-3"
                >
                  <div class="card-header text-center">Detail Tagihan</div>
                  <div class="card-body">
                    <div class="row">
                      <div class="col-6">ID Reff</div>
                      <div class="col-6">
                        {{ this.hasilCheck.responseData.ref_id }}
                      </div>
                    </div>
                    <div class="row">
                      <div class="col-6">No Pelanggan</div>
                      <div class="col-6">
                        {{ this.hasilCheck.responseData.no_pelanggan }}
                      </div>
                    </div>
                    <div class="row">
                      <div class="col-6">Nama Pelanggan</div>
                      <div class="col-6">
                        {{ this.hasilCheck.responseData.nama_pelanggan }}
                      </div>
                    </div>
                    <div class="row">
                      <div class="col-6">Periode</div>
                      <div class="col-6">
                        {{ this.hasilCheck.responseData.periode }}
                      </div>
                    </div>
                    <div class="row">
                      <div class="col-6">Tagihan</div>
                      <div class="col-6">
                        Rp. {{ this.hasilCheck.responseData.jumlah_tagihan }}
                      </div>
                    </div>
                    <div class="row">
                      <div class="col-6">Biaya Admin</div>
                      <div class="col-6">
                        Rp. {{ this.hasilCheck.responseData.biaya_admin }}
                      </div>
                    </div>
                    <div class="row">
                      <div class="col-6">Total Tagihan</div>
                      <div class="col-6">
                        Rp. {{ this.hasilCheck.responseData.total_tagihan }}
                      </div>
                    </div>
                    <div class="row">
                      <div class="col-6">Fee</div>
                      <div class="col-6">
                        Rp. {{ this.hasilCheck.responseData.fee }}
                      </div>
                    </div>
                    <div class="row">
                      <div class="col-6"><strong>Total Bayar</strong></div>
                      <div class="col-6">
                        Rp. {{ this.hasilCheck.responseData.total_bayar }}
                      </div>
                    </div>
                    <div class="row">
                      <div class="col-12"><hr /></div>
                    </div>
                    <div class="row">
                      <div class="col-12">
                        <div
                          v-if="this.user.verified == '1'"
                          class="form-group"
                        >
                          <label for="metode_pembayaran"
                            >Metode Pembayaran</label
                          >
                          <select
                            v-model="metodePembayaran"
                            id="metode_pembayaran"
                            name="metode_pembayaran"
                            required
                            class="form-control form-control-lg rounded-10"
                          >
                            <option value="">Pilih</option>
                            <option :value="'Hutang'">Bayar Nanti</option>
                            <option :value="'Dompet'">
                              Dompet | Saldo Anda: Rp.
                              {{ numberFormat(this.user.saldo) }}
                            </option>
                          </select>
                        </div>
                      </div>
                      <div class="col-12">
                        <button
                          type="button"
                          class="btn btn-lg btn-block btn-primary rounded-10"
                          @click="kirim()"
                        >
                          Bayar
                        </button>
                      </div>
                    </div>
                  </div>
                </div>
              </template>
            </form>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { getOperator } from "no-telp";
import Swal from "sweetalert2";
import axios from "axios";

export default {
  name: "CusTransaksiTLPascaView",
  props: ["ipAdress"],
  data() {
    return {
      user: JSON.parse(localStorage.getItem("SessionUser")),
      noTujuan: null,
      noPlNValid: false,
      loadCheckSpinner: false,
      hasilCheck: null,

      dataCheckNoTujuan: {},
      idOperator: null,
      idProduct: null,
      metodePembayaran: null,
      dataLoadProduct: null,
    };
  },
  methods: {
    kirim() {
      if (this.user.verified == "0") {
        this.metodePembayaran = "Dompet";
      }
      if (!this.metodePembayaran) {
        Swal.fire({
          icon: "error",
          title: "Metode Pembayaran Kosong",
          text: "silahkan pilih metode pembayaran!",
          showConfirmButton: false,
          timer: 1500,
        });
      } else {
        Swal.fire({
          title: "Yakin untuk mengirim?",
          text: "Pastikan data form sudah benar!",
          icon: "warning",
          showCancelButton: true,
          confirmButtonColor: "#3085d6",
          cancelButtonColor: "#d33",
          confirmButtonText: "Yakin",
        }).then((result) => {
          if (result.isConfirmed) {
            const formData = new FormData();
            formData.append("metode_pembayaran", this.metodePembayaran);
            formData.append("ref_id", this.hasilCheck.responseData.ref_id);
            formData.append(
              "no_pelanggan",
              this.hasilCheck.responseData.no_pelanggan
            );
            formData.append(
              "nama_pelanggan",
              this.hasilCheck.responseData.nama_pelanggan
            );
            formData.append(
              "total_tagihan",
              this.hasilCheck.responseData.total_tagihan
            );
            formData.append(
              "total_bayar",
              this.hasilCheck.responseData.total_bayar
            );
            formData.append("cus_id", this.user.id);

            axios({
              method: "post",
              url:
                this.ipAdress +
                "api/transaksi/tlpasca?username=" +
                this.user.username,
              data: formData,
              headers: { "Content-Type": "multipart/form-data" },
            })
              .then(function (response) {
                //handle success
                // console.log(response);
                if (response.data.status == "failed") {
                  Swal.fire({
                    icon: "error",
                    title: response.data.message,
                    text: response.data.keterangan,
                    showConfirmButton: true,
                    timer: 1500,
                  }).then(function () {
                    window.location = "/customer/data-transaksi";
                  });
                } else if (response.data == "") {
                  Swal.fire({
                    icon: "success",
                    title: "Diproses",
                    text: "Transaksi Diproses",
                    showConfirmButton: true,
                    timer: 1500,
                  }).then(function () {
                    window.location = "/customer/data-transaksi";
                  });
                } else {
                  Swal.fire({
                    icon: "success",
                    title: response.data.message,
                    text: response.data.keterangan,
                    showConfirmButton: true,
                    timer: 1500,
                  }).then(function () {
                    window.location = "/customer/data-transaksi";
                  });
                }
              })
              .catch(function (response) {
                //handle error
                console.log(response);
              });
          }
        });
      }
    },

    numberFormat(x) {
      if (x) {
        return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ".");
      } else {
        return (x = "0");
      }
    },

    checkDigit: function () {
      this.hasilCheck = null;
      if (
        this.noTujuan.replace(/[^0-9]/g, "").length == 11 ||
        this.noTujuan.replace(/[^0-9]/g, "").length == 12
      ) {
        this.noPlNValid = true;
      } else {
        this.noPlNValid = false;
      }
    },

    checkTagihan() {
      //551300218615
      // 223452244533
      // 22-3-45-22-44-5-3-3-dsadasdasdaw
      this.noTujuan = this.noTujuan.replace(/[^0-9]/g, "");
      this.loadCheckSpinner = true;
      axios({
        method: "post",
        url:
          this.ipAdress +
          "api/check-tagihan-pln/" +
          this.noTujuan +
          "?username=" +
          this.user.username,
        headers: { "Content-Type": "multipart/form-data" },
      })
        .then((response) => {
          console.log(response);
          this.hasilCheck = response.data;
          this.loadCheckSpinner = false;
          //handle success
        })
        .catch(function (response) {
          //handle error
          console.log(response);
        });
    },
  },
  mounted() {},
};
</script>

<style>
html,
body {
  text-align: center;
}

body {
  background-color: #ffffff;
}

.rounded-2 {
  border-radius: 8px;
}

.rounded-5 {
  border-radius: 10px;
}

.rounded-10 {
  border-radius: 20px;
}

#navbottom {
  border-radius: 30px;
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
}

.bg-biru {
  background-color: #1583dd;
}

.content {
  margin-top: 100px;
  margin-bottom: 130px;
}

.bg-putih {
  background-color: #ffffff;
}
#logo-operator {
  width: 30%;
}
</style>